    -webkit-box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.28);
            box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.28);
    width: 250px;
    -webkit-transition: all ease-in-out 0.4s;
    transition: all ease-in-out 0.4s;
}
#Navigator #Navigator-menu.menu-enter-to, #Navigator #Navigator-menu.menu-leave {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
}
#Navigator #Navigator-menu.menu-leave-to, #Navigator #Navigator-menu.menu-enter {
      -webkit-transform: translate(-100%, 0);
              transform: translate(-100%, 0);
}
#Navigator #Navigator-menu .title {
      background: rgba(0, 0, 0, 0.5);
      padding: 1rem;
      padding-left: 70px;
      color: white;
      margin-bottom: 1rem;
      font-size: 0.9rem;
}
#Navigator #Navigator-menu .single-input {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      margin-bottom: 10px;
}
#Navigator #Navigator-menu .single-input > input {
        width: 80%;
        padding: 5px 10px;
}
#Navigator #Navigator-menu .single-input > span {
        color: white;
