            align-self: flex-start;
        margin-top: 5px;
        margin-bottom: 5px;
        margin-left: 1.2rem;
        font-size: 0.8rem;
}
#Navigator #Navigator-menu .menu-contains {
      overflow-y: auto;
}
#Navigator #Navigator-menu .menu-contains::-webkit-scrollbar {
        width: 0px;
}
#Navigator #Navigator-menu .menu-contains .viewbox {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        padding: 0.6rem 1.2rem;
        color: white;
        font-size: 0.8rem;
        -webkit-transition: background-color 0.4s ease-in-out;
        transition: background-color 0.4s ease-in-out;
}
#Navigator #Navigator-menu .menu-contains .viewbox:hover {
