}
#Navigator #Navigator-menu .menu-contains .about h3 {
          font-weight: lighter;
          margin: 0.6rem 0;
}
#Navigator #Navigator-menu .menu-contains .about h3 small {
            color: #c2c2c2;
}
#Navigator #Navigator-menu .menu-contains .about .table {
          color: gray;
}
#Navigator .logo {
    position: fixed;
    left: 0;
    top: 0;
    height: 30px;
    width: auto;
    padding: 10px;
    margin-right: 1rem;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    z-index: 1000000;
    background: gray;
    border-radius: 25px;
}
#Navigator .logo.barShow {
      background: transparent;
      border-radius: 0;
}
#Navigator .logo:hover {
      background: #00c1de;
}

